import React, { Component } from 'react';
class Buys extends Component {
  constructor(props) {
    super(props)
    this.state = {
      amountIndex: 0,
      estopIndex: 0
    }
    this.changeAmount = this.changeAmount.bind(this);
    this.changeEstop = this.changeEstop.bind(this);

  }
  changeAmount(i) {
    this.setState({
      amountIndex: i,
      estopIndex: 0
    })
    setTimeout(() => { this.setPayAmount() }, 10);
  }
  changeEstop(i) {
    this.setState({
      estopIndex: i
    })
    setTimeout(() => { this.setPayAmount() }, 10);
  }

  setPayAmount() {
    let value = 0;
    let _self = this;
    this.props.buysData.forEach((item, index) => {
      if (index === _self.state.amountIndex) {
        value = item.cost + item.security[_self.state.estopIndex];
      }
    })
    _self.props.getPayAmount(value);
  }
  componentDidMount() {
    setTimeout(() => { this.setPayAmount() }, 10);
  }

  render() {
    return (
      <div className="buys">
        <div className="buysTit">
          <h2 className="name"></h2>
          <div className="times red"><span className="num">{this.props.buysTimes}</span></div>
        </div>
        <ul className="buysAmount">
          {
            this.props.buysData.map((item, index) => (
              <li className={index === this.state.amountIndex ? "item active" : "item"} key={index} onClick={this.changeAmount.bind(this, index)}>
                <span className="tap">{item.amount}</span>
              </li>
            ))
          }
        </ul>

        {
          this.props.buysData.map((item, index) => (
            index === this.state.amountIndex &&
            <div key={index}>
              <div className="buysItem">
                <span className="name"></span>
                <div className="text">
                  <span className="red">{item.funds_buy}</span><span className="red">{item.funds_rate}%</span>
                </div>
              </div>
              <div className="buysItem">
                <span className="name">   </span>
                <div className="text"><span className="red">{item.time}</span></div>
              </div>
              <div className="buysItem">
                <span className="name">   </span>
                <div className="text"><span className="red">{item.eprofit}</span></div>
              </div>
              <div className="buysItem">
                <span className="name">   </span>
              </div>
              <ul className="buysAmount">
                {
                  item.estop.map((itemsub, indexb) => (
                    <li className={indexb === this.state.estopIndex ? "item active" : "item"} key={indexb} onClick={this.changeEstop.bind(this, indexb)}>
                      <span className="tap">{itemsub}</span>
                    </li>
                  ))
                }
              </ul>
              <div className="buysItem">
                <span className="name"></span>
                <div className="text"><span className="red">{item.cost}</span></div>
              </div>
              <div className="buysItem">
                <span className="name"></span>
                <div className="text"><span className="red">{item.security[this.state.estopIndex]}</span></div>
              </div>
              <div className="buysItem">
                <span className="name">   </span>
                <div className="text"><span className="red">{item.delay_condition[this.state.estopIndex]}</span></div>
              </div>
              <div className="buysItem">
                <span className="name">   </span>
                <div className="text"><span className="red">{item.delay_money}</span>/</div>
              </div>
            </div>
          ))
        }

      </div>
    );
  }
}

export default Buys;
