import React, { Component } from 'react';
import { Modal, Button } from 'antd-mobile';
import Sideslip from '@/components/sideslip';
const { sideslipShow } = Sideslip;
const alert = Modal.alert;
const prompt = Modal.prompt;
class Order extends Component {
  openDetail(id) {
    let contentText = this.props.stockData.map((item, index) => {
      if (index === id) {
        return <div className="sell-detail" key={index}>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.buy_time}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.number}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.security}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.delay_money}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.estop}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.delay_day}</span>
          </div>
        </div>
      }
      return false;
    })
    sideslipShow({ contentText: contentText, title: "" })
  }

  render() {
    return (
      <ul className="sell-order">
        {
          this.props.stockData.map((item, index) => (
            <li className="item" key={index}>
              <div className="hd">
                <div className="stock">
                  <p className="name">{item.name}</p>
                  <p className="code">{item.code}</p>
                </div>
                <div className="counts">
                  <p className="price">{item.buy_price}</p>
                  <p className="price">{item.now_price}</p>
                  <p className="amount">{item.amount} {item.shares}()</p>
                </div>
                <div className={item.loss > 0 ? "loss red" : "loss green"}>{item.loss}</div>
              </div>
              <div className="btn-box">
                <Button className="btns" onClick={this.openDetail.bind(this, index)}></Button>
                <Button className="btns" onClick={() =>
                  alert('', '', [
                    { text: '', onPress: () => console.log('cancel') },
                    { text: '', onPress: () => console.log('ok') },
                  ])
                }></Button>
                <Button className="btns" onClick={() => prompt('', '', [
                  { text: '', onPress: () => console.log('cancel') },
                  { text: '', onPress: () => console.log('ok') },
                ], 'default', null, [''])}></Button>
              </div>
            </li>
          ))
        }
      </ul>
    );
  }
}

export default Order;
