import React, { Component } from 'react';
import { Button } from 'antd-mobile';
import Sideslip from '@/components/sideslip';
const { sideslipShow } = Sideslip;
class Order extends Component {
  openDetail(id) {
    let contentText = this.props.stockData.map((item, index) => {
      if (index === id) {
        return <div className="sell-detail" key={index}>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.amount}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.shares}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.buy_time}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.buy_time}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.buy_price}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.now_price}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.loss}</span>
          </div>
          <div className="item">
            <span className="name"></span>
            <span className="dsp">{item.security}</span>
          </div>
        </div>
      }
      return false;
    })
    sideslipShow({ contentText: contentText, title: ""})
  }

  render() {
    return (
      <ul className="settle-order">
        {
          this.props.stockData.map((item, index) => (
            <li className="item" key={index}>
              <div className="info">
                <span className="time">{item.buy_time}</span>
                <span className="number">{item.number}</span>
              </div>
              <div className="hd">
                <div className="stock">
                  <p className="name">{item.name}</p>
                  <p className="code">{item.code}</p>
                </div>
                <div className="counts">
                  <p className="price"><b className={item.loss > 0 ? "red" : "green"}>{item.loss}</b></p>
                  <p className="price"><b className={item.loss > 0 ? "red" : "green"}>{item.loss > 0 ? item.loss : '0.00'}</b></p>
                  <p className="amount">{item.amount} {item.shares}()</p>
                </div>
                <div className="btn-box">
                  <Button className="btns" onClick={this.openDetail.bind(this, index)}></Button>
                </div>
              </div>

            </li>
          ))
        }
      </ul>
    );
  }
}

export default Order;
