import React, { Component } from 'react';
class Spread extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ewm: require('../../static/images/ewm.png')
        }
    }
    render() {
        return (
            <div className="spread">
                <div className="title red"></div>
                <div className="bt c2"></div>
                <div className="ewm"><img src={this.state.ewm} alt=''></img></div>
                <div className="code"><b className="red">UJRXVS</b></div>
                <button className="btn btn-outlined"></button>
            </div>
        )
    }
}
export default Spread;